import { LabelType, Options } from '@angular-slider/ngx-slider';
import { HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Annonce } from 'src/app/core/entity/annonce/annonce';
import { DataTable } from 'src/app/core/entity/data-table';
import { Job } from 'src/app/core/entity/job/job';
import { PickList } from 'src/app/core/entity/pickList/pickList';
import { TableParams } from 'src/app/core/entity/table-params';
import { LanguageEnum } from 'src/app/core/enumaration/language-enum';
import { FormDataPipe } from 'src/app/core/pipes/form-data.pipe';
import { AnnonceService } from 'src/app/core/service/annonce/annonce.service';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { JobService } from 'src/app/core/service/job/job.service';
import { LanguageService } from 'src/app/core/service/language.service';
import { LoadingService } from 'src/app/core/service/loading.service';
import { MenuService } from 'src/app/core/service/menu.service';
import { PickListService } from 'src/app/core/service/pick-list.service';
import { environment } from 'src/environments/environment';
import { LANGUAGE_EN } from '../../theme/languages/en';
import { LANGUAGE_FR } from '../../theme/languages/fr';
import { LANGUAGE_MG } from '../../theme/languages/mg';

@Component({
  selector: 'app-annonce-home',
  templateUrl: './annonce-home.component.html',
  styleUrls: ['./annonce-home.component.scss']
})
export class AnnonceHomeComponent implements OnInit {
  image_uri: string = environment.api_host + environment.image_url;
  annonces: Array<Annonce> = [];
  opacityVisible: string = "opacity-0 invisible";
  jobs: Array<Job> = [];
  language: any = {};
  constructor(
    private annonceService: AnnonceService,
    private loadingService: LoadingService,
    private router: Router,
    private jobService: JobService,
    private pickListService: PickListService,
    private languageService: LanguageService,
    public authService: AuthService,
    private menuService: MenuService,
  ) {
    if (this.languageService.GetLanguage() == LanguageEnum.FR) {
      this.language = LANGUAGE_FR;
    }
    if (this.languageService.GetLanguage() == LanguageEnum.EN) {
      this.language = LANGUAGE_EN;
    }
    this.tableParams.draw = 20;
    this.tableParams.start = 0;
    this.tableParams.length = 20;
  }
  categories: Array<PickList> = [];
  tableParams: TableParams = new TableParams();
  formDataPipe = new FormDataPipe();
  menus: Array<any> = [];
  ngOnInit(): void {
    this.getMenus();
    // this.annonceService.GetPaginated(false, this.formDataPipe.transform(this.tableParams)).subscribe((response: HttpResponse<DataTable>) => {
    //   if (response.body['data']['data'].length > 0) {
    //     this.isAnnonce = true;
    //     this.annonces = response.body['data']['data'];
    //   } else {
    //     this.isAnnonce = false;
    //   }
    //   this.loadingService.emitChange(false);
    // });
    this.getPaginatedAnnonce();
  }
  getMenus() {
    this.menuService.getMenu(false, 'ANNONCES_PETITES_ANNONCES').subscribe((response: HttpResponse<any>) => {
      this.menus = response.body['data'];
    }, error => {
      this.loadingService.emitChange(false);
    })
  }

  onClickCreateAnnonceSevicesButton() {
    this.router.navigateByUrl(environment.uri_create_annonce);
  }
  onClickDetail(annonce) {
    this.router.navigateByUrl(
      environment.uri_annonce_detail, {
      state: annonce as Annonce
    });
  }
  menuCategoryButton() {
    if (this.isCategoryMenu.length > 0) {
      this.isCategoryMenu = "";
    } else {
      this.isCategoryMenu = this.opacityVisible;
    }
  }
  minValue: number = 100;
  maxValue: number = 114500000;
  isCategoryMenu = this.opacityVisible;
  options: Options = {
    floor: 1000,
    ceil: 114500000,
    translate: (value: number, label: LabelType): string => {
      switch (label) {
        case LabelType.Low:
          return '<b class="text-bold text-yellow-700">Min:</b> Ar ' + value;
        case LabelType.High:
          return '<b class="text-bold text-yellow-700">Max:</b> Ar ' + value;
        default:
          return 'Ar' + value;
      }
    }
  };
  selectedCategory = "Catégorie";


  pageNumber: number = 0;
  itemNumber: number = 20;
  isAnnonce: boolean = false;
  increments() {
    this.pageNumber++;
    this.tableParams.draw = this.tableParams.draw * 2;
    this.getPaginatedAnnonce();
  }
  decrements() {
    this.pageNumber--;
    this.tableParams.draw = this.tableParams.draw / 2;
    this.getPaginatedAnnonce();
  }
  getPaginatedAnnonce() {
    this.tableParams.start = this.pageNumber;
     this.tableParams['search[value]'] = '';
    this.annonceService.GetPaginated(false, this.formDataPipe.transform(this.tableParams)).subscribe((response: HttpResponse<DataTable>) => {
      if (response.body['data']['data'].length > 0) {
        this.isAnnonce = true;
        this.annonces = response.body['data']['data'];
      } else {
        this.isAnnonce = false;
      }
      this.loadingService.emitChange(false);
    });
  }


  searchValue: string = "";
  onSearch(){
    this.tableParams['search[value]'] = this.searchValue;
    this.getPaginatedAnnonce();
  }
  filterByMenu(menu){
    this.tableParams['search[value]'] = menu.value;
    this.getPaginatedAnnonce();
  }
  searchInputChange(){
    if(!this.searchValue){
      this.tableParams['search[value]'] ="";
      this.getPaginatedAnnonce();
    }
  }
}

