import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AnnonceCreateComponent } from './annonce-create/annonce-create.component';
import { AnnonceDetailComponent } from './annonce-detail/annonce-detail.component';
import { AnnonceHomeComponent } from './annonce-home/annonce-home.component';

const routes: Routes = [
    {path:'',pathMatch:'full',redirectTo:'annonce-home'},
    {path:'annonce-home', component: AnnonceHomeComponent},
    {path: 'annonce-detail', component: AnnonceDetailComponent},
    {path: 'annonce-create', component: AnnonceCreateComponent},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AnnonceRoutingModule { }
