import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActionConfig } from 'src/app/core/entity/action-config';
import { Annonce } from 'src/app/core/entity/annonce/annonce';
import { AnnonceService } from 'src/app/core/service/annonce/annonce.service';
import { Location } from '@angular/common';
import { Vente } from 'src/app/core/entity/vente/vente';
import { PickList } from 'src/app/core/entity/pickList/pickList';
import { Images } from 'src/app/core/entity/images/images';
import { User } from 'src/app/core/entity/user/user';
import { environment } from 'src/environments/environment';
import { SweetAlertService } from 'src/app/core/service/sweet-alert.service';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { FollowAnnonce } from 'src/app/core/entity/follow-annonce';
import { HttpResponse } from '@angular/common/http';
import { LoadingService } from 'src/app/core/service/loading.service';
import { HTML_EMOJI } from '../../theme/items/emojis/html-emoji';

@Component({
  selector: 'app-annonce-detail',
  templateUrl: './annonce-detail.component.html',
  styleUrls: ['./annonce-detail.component.scss']
})
export class AnnonceDetailComponent implements OnInit {
  image_uri: string = environment.api_host + environment.image_url;
  public User: User = new User()
  actionConfigs: Array<ActionConfig> = new Array<ActionConfig>();
  private state$: Observable<Annonce>;
  constructor(
    private activatedRouter: ActivatedRoute,
    private location: Location,
    private sweetAlertService: SweetAlertService,
    private authService: AuthService,
    private annonceService: AnnonceService,
    private loadingService: LoadingService
  ) { }
  annonce: Annonce = new Annonce();
  ngOnInit(): void {
    this.state$ = this.activatedRouter.paramMap.pipe(map(() => window.history.state));
    this.state$.subscribe(annonce => {
      if (annonce.annonce_id != null) {
        this.annonce = annonce;
        this.thumbBigImage = this.image_uri + annonce.images[0].value;
      } else {
        this.location.back();
      }
    });
  }

  thumbBigImage: string = "";
  onClickThumbNail(data) {
    this.thumbBigImage = this.image_uri + data.value;
  }
  onClickAcheter(owner) {
    console.log(owner);
    let html1 = "<p class='text-bold text-xl'>"+ HTML_EMOJI.PHONE + " " + owner.phone + "</p>";
    let html2 = "<p class='text-bold text-xl'>" + HTML_EMOJI.EMAIL + " " + owner.email + "</p>";

    this.sweetAlertService.showModal(html1 + html2);
  }
  follwerReq: FollowAnnonce = new FollowAnnonce();
  onClickFollow(annonce) {
    this.follwerReq.annonce_id = annonce.annonce_id;
    this.follwerReq.follower_id = this.authService.GetUserParam('user_id');

    this.annonceService.Follow(true, this.follwerReq).subscribe((response: HttpResponse<string>) => {
      this.sweetAlertService.showToastr(response.body['data']);
      this.loadingService.emitChange(false);
    }, error => {
      this.loadingService.emitChange(false);
    });
  }
  onClickUnFollow(annonce) {
    this.follwerReq.annonce_id = annonce.annonce_id;
    this.follwerReq.follower_id = this.authService.GetUserParam('user_id');

    this.annonceService.Follow(true, this.follwerReq).subscribe((response: HttpResponse<string>) => {
      this.sweetAlertService.showToastr(response.body['data']);
      this.loadingService.emitChange(false);
    }, error => {
      this.loadingService.emitChange(false);
    });
  }

}
