import { HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { Annonce } from 'src/app/core/entity/annonce/annonce';
import { NewAnnonceReq } from 'src/app/core/entity/annonce/new-annonce-req';
import { ItemsNumber } from 'src/app/core/entity/items-number';
import { Job } from 'src/app/core/entity/job/job';
import { Payment } from 'src/app/core/entity/payment';
import { PickList } from 'src/app/core/entity/pickList/pickList';
import { UserLoginReq } from 'src/app/core/entity/user/user-login-req';
import { AnnonceService } from 'src/app/core/service/annonce/annonce.service';
import { AuthService } from 'src/app/core/service/authentication/auth.service';
import { ItemsNumberService } from 'src/app/core/service/items-number.service';
import { JobService } from 'src/app/core/service/job/job.service';
import { LoadingService } from 'src/app/core/service/loading.service';
import { MenuService } from 'src/app/core/service/menu.service';
import { PaymentService } from 'src/app/core/service/payment.service';
import { PickListService } from 'src/app/core/service/pick-list.service';
import { SweetAlertService } from 'src/app/core/service/sweet-alert.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-annonce-create',
  templateUrl: './annonce-create.component.html',
  styleUrls: ['./annonce-create.component.scss']
})
export class AnnonceCreateComponent implements OnInit {

  constructor(
    private annonceService: AnnonceService,
    private loadingService: LoadingService,
    private jobService: JobService,
    private pickListService: PickListService,
    private formBuilder: FormBuilder,
    private toastrService: ToastrService,
    private router: Router,
    private sweetAlertService: SweetAlertService,
    private itemsNumberService: ItemsNumberService,
    private paymentService: PaymentService,
    private authServie: AuthService,
    private menuService: MenuService
  ) { }
  is_service: boolean = false;
  is_annonce: boolean = true;
  active: string = "text-gray-500 bg-gray-200";
  annone_active: string = "text-gray-500 bg-gray-200";
  service_active: string = "";
  paymentMethod: string = "Mode de payement";
  duree: string = "Selectionez la durée";
  image_1_label: string = "Image";
  image_2_label: string = "Image";
  image_3_label: string = "Image";
  image_4_label: string = "Image";
  uploadForm: FormGroup;
  result: Subject<string> = new Subject();
  content: string = '';
  annonce: Annonce = new Annonce();
  newAnnonceReq: NewAnnonceReq = new NewAnnonceReq();

  jobs: Array<Job> = [];
  annonces: Array<Annonce> = [];
  categories: PickList[] = [];
  category: string = "Categorie";
  f = {}
  itemsNumber: ItemsNumber = new ItemsNumber();
  annoncePrice: number = 300000;
  ngOnInit(): void {
    this.getMenus();
    this.uploadForm = this.formBuilder.group({
      File_1: [''],
      File_2: [''],
      File_3: [''],
      File_4: [''],
    });
    this.pickListService.getPickListByGroup(false, 'ANNONCES_CATEGORY').subscribe((response: HttpResponse<PickList[]>) => {
      this.categories = response.body['data'];
      this.loadingService.emitChange(false);
    })
    this.jobService.GetLimitedJob(false, 4).subscribe((response: HttpResponse<Job[]>) => {
      this.jobs = response.body['data'];
      this.loadingService.emitChange(false);
    });
  }
  menus: Array<any> = [];
  getMenus() {
    this.menuService.getByGeoupe(false, 'ANNONCES').subscribe((response: HttpResponse<any>) => {
      this.menus = response.body['data'];
    }, error => {
      this.loadingService.emitChange(false);
    })
  }
  save() {
    if (this.category != "Categorie"
      && this.uploadForm.get("File_1").value
      && this.uploadForm.get("File_2").value
      && this.uploadForm.get("File_3").value
      && this.uploadForm.get("File_4").value
    ) {
      let formData = new FormData();
      formData.append('File_1', this.uploadForm.get('File_1').value);
      formData.append('File_2', this.uploadForm.get('File_2').value);
      formData.append('File_3', this.uploadForm.get('File_3').value);
      formData.append('File_4', this.uploadForm.get('File_4').value);
      this.newAnnonceReq.user_id = this.authServie.GetUserParam('user_id');
      //this.newAnnonceReq.state_id = environment.state_id;
      //this.newAnnonceReq.category_id = environment.category_id;
      formData.append('JsonBody', JSON.stringify(this.newAnnonceReq) as any);

      let payment = new Payment();
      payment.entityName = "annonce";
      payment.status = true;
      payment.entity = formData;
      this.paymentService.emitChange(payment);
      this.loadingService.emitChange(false);
    } else {
      this.showWarning("Veuillez remplir tous les champs!");
    }
  }


  onClickPayment(operator) {
    this.paymentMethod = operator;
  }
  onClickDuree(duree) {
    this.duree = duree;
  }

  onClickTab(data) {
    switch (data) {
      case "annonce":
        this.is_annonce = !this.is_annonce;
        this.is_service = !this.is_service;
        this.service_active = "";
        this.annone_active = this.active;
        break;
      case "service":
        this.is_annonce = !this.is_annonce;
        this.is_service = !this.is_service;
        this.annone_active = "";
        this.service_active = this.active;
        break;
    }
  }
  onClickCategory(catg) {
    if (catg != "Categorie") {
      this.category = catg.value;
      this.newAnnonceReq.menu_id = catg.menu_id;
    } else {
      this.category = "Categorie"
    }
  }


  onFile1Selected(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.image_1_label = file.name;
      this.uploadForm.get('File_1').setValue(file);
    } else {
      this.showWarning("vous dever selectionner un fichier");
    }
  }
  onFile2Selected(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.image_2_label = file.name;
      this.uploadForm.get('File_2').setValue(file);
    } else {
      this.showWarning("vous dever selectionner un fichier");
    }
  }
  onFile3Selected(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.image_3_label = file.name;
      this.uploadForm.get('File_3').setValue(file);
    } else {
      this.showWarning("vous dever selectionner un fichier");
    }
  }
  onFile4Selected(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.image_4_label = file.name;
      this.uploadForm.get('File_4').setValue(file);
    } else {
      this.showWarning("vous dever selectionner un fichier");
    }
  }
  showWarning(message) {
    this.toastrService.warning(message, '');
  }
  onClickJobDetails(job) {
    this.router.navigateByUrl(
      environment.uri_job_details, {
      state: job as Job
    });
  }
}
